<template lang="en">
<div class="my-popups">
    <loader :is-visible="isLoading"></loader>
    <div class="modal d-block">
        <div class="modal-dialog modal-dialog-centered">
            <div class="modal-content">
                <div class="modal-header theme-primary partition-full d-flex">
                    <h4 class="modal-title">{{ $t('SelectOrganization') }}</h4>
                    <button type="button" class="close" v-on:click="Close">
                        <em class="fa fa-times" aria-hidden="true"></em>
                    </button>
                </div>
                <div class="modal-body">
                    <div class="row">
                        <div v-for="item in TenantObjectData" :key="item.TenantId" class="form-group d-flex flex-column align-items-start mb-4 col-md-4">
                            <label class="mr-2">{{ item.TenantName }}</label>
                            <label class="switch">
                                <input type="checkbox" v-model="item.selected" @change="onToggleChange(item)"/>
                                <span class="slider round">
                                    <span class="slider-yes">Yes</span> 
                                    <span class="slider-no">No</span>
                                </span>
                            </label>
                        </div>
                    </div>
                    <div class="mt-3 d-flex justify-content-end">
                        <button class="btn btn-success mr-2" @click="onSubmit">
                            <em class="fa fa-save pr-2"></em>{{ $t("Save") }}
                        </button>
                        <button class="btn btn-danger" @click="Close">
                            <em class="fa fa-close pr-2"></em>{{ $t("Close") }}
                        </button>
                    </div>
                </div>
            </div>
        </div>
    </div>
</div>
</template>

<script>
import DataService from "../../services/DataService";

export default {
    props: {
        TenantData: {
            type: Array, 
            required: true
        }
    },
    data() {
        return {
            isLoading: false,
            toggleSelected: false,
            TenantObjectData: [],
            XeroId: ''
        };
    },
    created() {
        var vm = this;
        vm.TenantObjectData = JSON.parse(vm.TenantData.filePath);
        vm.XeroId = vm.TenantData.code;   
    },
    methods: {
        onToggleChange(changedItem) {
            var vm = this;
            vm.TenantObjectData.forEach(item => {
                item.selected = (item === changedItem); 
            });
        },
        async onSubmit() {
            var vm = this;
            var selectedTenant = vm.TenantObjectData.find(item => item.selected);
            var params = `id=${vm.XeroId}&tenantid=${selectedTenant.TenantId}`
            await DataService.SaveTenant(params).then((response) => {
                if (response.data.status == 'Success') {
                    vm.ShowAlert(vm.$t("OrganizationSaved"), "success", true, vm.$t("Alert"));  
                    vm.Close();
                    vm.$parent.FetchData();            
                } 
            })
        },
        Close() {
            this.$parent.IsTenantPopup = false;
        }
    }
};
</script>