<template>
<div class="my-popups">
    <div class="modal d-block">
        <div class="modal-dialog modal-dialog-centered ui-draggable">
            <loader :is-visible="isLoading" />
            <div class="modal-content">
                <div class="modal-header ui-draggable-handle">
                    <h4 class="modal-title">{{ $t('IntegrateAccount') }}</h4>
                    <button class="close" @click="ClosePopup">
                        <em class="fa fa-times" aria-hidden="true"></em>
                    </button>
                </div>

                <div class="modal-body">
                    <Form ref="IntegrateAccount">
                        <!-- Name Input -->
                        <!-- <Field name="Name" v-slot="{ errors }" :rules="{ required: true }" v-if="IsWorkDay">
                            <div class="form-group">
                                <label for="Name">{{ $t('Name') }}:</label><span class="text-danger">*</span>
                                <input type="text" id="Name" placeholder="Enter Name" v-model="Name" :class="{ 'form-control': true, 'is-invalid': errors[0] }" />
                                <span v-if="errors[0]" class="invalid-feedback d-block">{{ errors[0] }}</span>
                            </div>
                        </Field> -->

                        <!-- Drive Selection Buttons -->
                        <div class="row" v-if="!IsWorkDay">
                            <div class="col-lg-4 mb-3 text-center" v-for="option in driveOptions" :key="option.Name">
                                <button class="btn btn-outline-primary w-100 d-flex align-items-center justify-content-center py-2" :class="{ active: drive === option.Name }" @click="SelectDrive(option.Name)">
                                    <img :src="option.logo" alt="" style="height: 24px; margin-right: 8px;" />
                                    <span>{{ option.Name }}</span>
                                </button>
                            </div>
                        </div>

                        <!-- Client ID & Secret -->
                        <div class="row" v-if="IsWorkDay">
                            <div class="col-lg-6">
                                <Field name="client Id" v-slot="{ errors }" :rules="{ required: true }">
                                    <div class="form-group">
                                        <label>{{ $t('ClientID') }}:</label><span class="text-danger">*</span>
                                        <input type="text" class="form-control" v-model="WD_clientId" :class="{ 'is-invalid': errors[0] }" />
                                        <span v-if="errors[0]" class="invalid-feedback d-block">{{ errors[0] }}</span>
                                    </div>
                                </Field>
                            </div>

                            <div class="col-lg-6">
                                <Field name="Client Secrat" v-slot="{ errors }" :rules="{ required: true }">
                                    <div class="form-group">
                                        <label>{{ $t('ClientSecret') }}:</label><span class="text-danger">*</span>
                                        <input type="text" class="form-control" v-model="WD_ClientSecrat" :class="{ 'is-invalid': errors[0] }" />
                                        <span v-if="errors[0]" class="invalid-feedback d-block">{{ errors[0] }}</span>
                                    </div>
                                </Field>
                            </div>

                            <div class="col-lg-6">
                                <Field name="redirect Uri" v-slot="{ errors }" :rules="{ required: true }">
                                    <div class="form-group">
                                        <label>{{ $t('RedirectURI') }}:</label><span class="text-danger">*</span>
                                        <input type="text" class="form-control" v-model="WD_redirectUri" :class="{ 'is-invalid': errors[0] }" />
                                        <span v-if="errors[0]" class="invalid-feedback d-block">{{ errors[0] }}</span>
                                    </div>
                                </Field>
                            </div>
                        </div> 
                    </Form>
                </div>

                <!-- Footer -->
                <div class="modal-footer text-right" v-if="IsWorkDay">
                    <button @click="IntegrateAccount" class="btn btn-success">
                        <em class="fa fa-floppy-o pr-2"></em> {{ $t('Save') }}
                    </button>
                    <button @click="ClosePopup" class="btn btn-danger">
                        <em class="fa fa-times pr-2" aria-hidden="true"></em> {{ $t('Cancel') }}
                    </button>
                </div>
            </div>
        </div>
    </div>
</div>
</template>

<script>
import DataService from "../../services/DataService";

export default {
    props: {
        AccessToken: {
            type: [String, Boolean],
            required: false,
        }
    },
    data() {
        return {
            Name: "",
            drive: "",
            isLoading: false,
            clientId: "",
            ClientSecrat: "",
            //Redirect_Uri: "https://7dbc-202-164-57-197.ngrok-free.app/Billing/IntegrateAccount",
            Redirect_Uri: "https://stage.talygen.com/Billing/IntegrateAccount",
            //Redirect_Uri: `${window.location.origin}/Billing/IntegrateAccount`,
            WD_clientId: '',
            WD_ClientSecrat: '',
            WD_redirectUri: '',
            Webhooks_key: '',
            xero_ClientSecrat: '',
            xero_clientId: '',
            EndAddress: '',
            username: '',
            password: '',
            driveOptions: [],
            IsWorkDay: false,
            IsXero: false,
            driveName: '',
            AccessTokens: '',
            AccessTokenExpiresAt: '',
            RefreshToken: '',
            RefreshTokenExpiresAt: '',
            ModifiedAt: '',
            XeroTokenJson: '',
        };
    },
    created: async function () {
        var vm = this;
        vm.GetIntegrationData();
    },
    methods: {
        ClosePopup() {
            this.$parent.IsAccountintegration = false;
        },
        async GetIntegrationData(item) {
            var vm = this;
            await DataService.GetIntegrationDetail().then((response) => {
                if(response.data){
                    vm.driveOptions= response.data;
                     vm.driveOptions.forEach(element => {
                        if(element.Name =='Xero'){
                            vm.xero_clientId = element.client_id;
                            vm.xero_ClientSecrat = element.client_secret;
                            vm.xero_redirect_uri = element.redirect_uri;
                            vm.Webhooks_key= element.Webhooks_Key
                        }
                    });
                }
            })
        },
        async IntegrateAccount() {
            this.isLoading = true;
            var vm = this;
            var driveName = '';
            if (vm.IsWorkDay) {
                driveName = 'WorkDay';
            } else if (vm.IsXero) {
                driveName = 'Xero';
            }
            vm.$refs.IntegrateAccount.validate().then(async (valid) => {
                if (valid) {
                    var param = '';
                    if (driveName === 'WorkDay') {
                        var param = `DriveName=${driveName}&logo=&ClientId=${vm.WD_clientId}&ClientSecret=${vm.WD_ClientSecrat}&CallbackUri=${vm.Redirect_Uri}&Name=${driveName}&WebhooksKey=${vm.Webhooks_key}&EndAddress=${vm.EndAddress}&username=${vm.username}&password=${vm.password}&AccessToken=${vm.AccessTokens}&AccessTokenExpiresAt=${vm.AccessTokenExpiresAt}&RefreshToken=${vm.RefreshToken}&RefreshTokenExpiresAt=${vm.RefreshTokenExpiresAt}&ModifiedAt=${vm.ModifiedAt}&XeroTokenJson=${vm.XeroTokenJson}`;
                    } else if (driveName === 'Xero') {
                        var param = `DriveName=${driveName}&logo=&ClientId=${vm.xero_clientId}&ClientSecret=${vm.xero_ClientSecrat}&CallbackUri=${vm.Redirect_Uri}&Name=${driveName}&WebhooksKey=${encodeURIComponent(vm.Webhooks_key)}&EndAddress=${vm.EndAddress}&username=${vm.username}&password=${vm.password}&AccessToken=${vm.AccessTokens}&AccessTokenExpiresAt=${vm.AccessTokenExpiresAt}&RefreshToken=${vm.RefreshToken}&RefreshTokenExpiresAt=${vm.RefreshTokenExpiresAt}&ModifiedAt=${vm.ModifiedAt}&XeroTokenJson=${vm.XeroTokenJson}`;
                    }
                    await DataService.SaveDriveCredential(param).then(async (response) => {
                        if (response.data.Status === "Success") {
                            let resultString = response.data.Result;
                            let resultObj = JSON.parse(resultString);
                            let code = resultObj.CODE;
                            if (code == 1) {
                                vm.ShowAlert(vm.$t("AddedSuccess", [vm.$t("Integration")]), "Success", true, vm.$t("Alert"));
                                vm.ClosePopup();
                                vm.$parent.FetchData();
                            } else if (code == 2) {
                                vm.ShowAlert(`${vm.driveName} Account Already Integrated`, "warning", vm.$t("Alert"));
                            }
                        }
                    });
                }
            });
            this.isLoading = false;
        },
        async SelectDrive(driveName) {
            const vm = this;
            this.drive = driveName;
            this.driveName = driveName;
            this.$emit('drive-selected', this.driveName); 
            if (driveName === "Talygen") {
                const param = `DriveName=${driveName}&logo=&ClientId=&ClientSecret=&CallbackUri=&Name=`;
                await DataService.SaveDriveCredential(param).then((response) => {
                    if (response.data.Status === "Success") {
                        const resultObj = JSON.parse(response.data.Result);
                        const code = resultObj.CODE;
                        vm.confirmR(vm.$t(`Are you sure you want to save ${driveName} record(s)?`), true, false, function () {
                            if (code === 1) {
                                vm.ShowAlert(vm.$t("AddedSuccess", [vm.$t("Integration")]), "Success", true, vm.$t("Alert"));
                                vm.ClosePopup();
                                vm.$parent.FetchData();
                            } else if (code === 2) {
                                vm.ShowAlert(`${vm.driveName} Account Already Integrated`, "warning", vm.$t("Alert"));
                            }
                        });
                    }
                });
            } 
            else if (driveName === "Xero") {
                const authUrl = new URL("https://login.xero.com/identity/connect/authorize");
                authUrl.searchParams.set("response_type", "code");
                authUrl.searchParams.set("client_id", vm.xero_clientId);
                authUrl.searchParams.set("redirect_uri", vm.xero_redirect_uri);
                authUrl.searchParams.set("scope", "openid profile email offline_access accounting.transactions accounting.attachments accounting.contacts");
                authUrl.searchParams.set("state", "xyz123");
                window.location.href = authUrl.toString();
            }
            else if (driveName === "Workday") {
                vm.IsWorkDay = true;
            }
        }
    }
};
</script>

<style scoped>
.modal .btn.active {
    border: 2px solid #007bff;
    font-weight: bold;
}

img {
    max-height: 24px;
}
</style>