<template>
    <section>
        <loader :is-visible="isLoading"></loader>
        <div class="ticketsmallfix theme-primary partition p-actions-expand mt-2" style="overflow: visible;">
            <span class="p-name text-white">{{ $t('AddIntegration') }}</span>
            <span class="p-actions float-right" @click="openModal">
                <a title="Add Integration" href="javascript:;" class="p-action-btn text-white ">
                    <em class="fa fa fa-plus"></em> <br>
                    <span>{{ $t('Add') }}</span>
                </a>
            </span>
        </div>
        <tg-list :IsShowAction="true" :showCheckBox="false" ModuleName="Billing" :listType="['List']"
            :RenderRowActionMethod="renderActions" SubModuleCode="Billing" IdentityColumn="id" :ListData="OuthList"
            :HeaderData="Headers" :callbackfunction="FetchData" :HeaderButtons="listheaderbuttons"
            :TotalRecords="TotalRecords" :ListDataCallBackFunction="FetchData" :LegendArray="[]" :SortExp="sortExp"
            :SortBy="sortBy" :NorecordfoundText="$t('NoRecordfound')" v-on:LeftsearchButtonClick="[]"
            v-on:PagerButtonClick="pagerMethod" :isShowHeaderText="true" v-on:ActionButtonClick="actionButtonClick"
            :HeaderText="$t('RateSchedulede')" v-on:HeaderButtonClick="commonHeaderButtonClick"
            v-on:SortdataButtonClick="sortdata">

            <template v-slot:slotdata="{ data }">
                <template v-if="data.column.COLUMN_NAME == 'created_at'">
                    <td class="text-center single-action tourguide_a" v-if="data.row.created_at">
                        {{ $d(data.row.created_at) }}
                    </td>
                </template>

                <template v-if="data.column.COLUMN_NAME == 'status_id'">
                    <span v-if="data.row.status_id == 1001" class="px-2 text-center text-white d-block bg-success">
                        {{ $t('Active') }}
                    </span>
                </template>

                <template v-if="data.column.COLUMN_NAME == 'TenantName'">
                    <span>{{ data.row.TenantName ? data.row.TenantName : '--' }}</span>
                </template>
            </template>
        </tg-list>
        <IntegrateAccount v-if="IsAccountintegration" :AccessToken="AccessToken" @drive-selected="onDriveSelected"
            @integration-complete="FetchData"></IntegrateAccount>
        <TenantPopup v-if="IsTenantPopup" :TenantData="TenantData"></TenantPopup>
    </section>
</template>

<script>
import TenantPopup from "../../components/Modals/TenantPopup.vue";
import IntegrateAccount from "../../components/Modals/AddAccountIntegration.vue";
import DataService from "../../services/DataService";
export default ({
    components: {
        IntegrateAccount,
        TenantPopup
    },
    data() {
        return {
            IsTenantPopup: false,
            IsAccountintegration: false,
            OuthList: [],
            TotalRecords: 0,
            pageSize: 10,
            pageNum: 1,
            sortBy: '',
            CurrentPage: 1,
            TotalPages: 0,
            sortExp: '',
            noRecords: false,
            Headers: [
                {
                    COLUMN_NAME: "name",
                    DATA_TYPE: "text",
                    SORTABLE: true,
                    VISIBLE: true,
                    DISPLAY_NAME: "name",
                    DISPLAY_ORDER: 1,
                },
                {
                    COLUMN_NAME: "TenantName",
                    DATA_TYPE: "text",
                    SORTABLE: true,
                    VISIBLE: true,
                    DISPLAY_NAME: "tenantname",
                    DISPLAY_ORDER: 2,
                },
                {
                    COLUMN_NAME: "created_at",
                    DATA_TYPE: "text",
                    SORTABLE: true,
                    VISIBLE: true,
                    DISPLAY_NAME: "CreatedAt",
                    DISPLAY_ORDER: 3,
                },
                {
                    COLUMN_NAME: "status_id",
                    DATA_TYPE: "text",
                    SORTABLE: true,
                    VISIBLE: true,
                    DISPLAY_NAME: "Status",
                    DISPLAY_ORDER: 4,
                }
            ],
            listActions: [{
                title: 'Refresh',
                href: 'javascript:void(0)',
                icon: 'text-success fa fa-refresh ',
                callbackfunction: 'Refreshtoken',
                additionalSpan: ''
            },
            {
                title: 'Delete',
                href: 'javascript:void(0)',
                icon: 'text-success fa fa-trash ',
                callbackfunction: 'delete',
                additionalSpan: ''
            }
            ],
            userId: this.GetUserInfo.ID,
            companyId: this.GetUserInfo.CompanyId,
            isLoading: false,
            AccessToken: '',
            clientId: '',
            ClientSecrat: '',
            redirectUri: '',
            Webhookskey: '',
            selectedDriveName: "",
            TenantData: []
        }
    },
    created: async function () {
        var vm = this;
        vm.FetchData();
        await vm.GetIntegrationData();
        vm.AccessToken = vm.$route.query.code;
        if (vm.AccessToken) {
            await vm.SaveXeroCredentials(vm.AccessToken);
        }
    },
    methods: {
        async GetIntegrationData(item) {
            var vm = this;
            await DataService.GetIntegrationDetail().then((response) => {
                if (response.data) {
                    vm.driveOptions = response.data;
                    vm.driveOptions.forEach(element => {
                        if (element.Name == 'Xero') {
                            vm.ClientId = element.client_id;
                            vm.ClientSecrat = element.client_secret;
                            vm.redirectUri = element.redirect_uri;
                            vm.Webhookskey = element.Webhooks_Key;
                        }
                    });
                }
            })
        },
        getTenantName(itm, tenantId) {
            try {
                const tenant = itm.find(tenant => tenant.TenantId === tenantId);
                return tenant ? tenant.TenantName : '';
            } catch (error) {
                console.error('Error parsing row:', error);
                return '';
            }
        },
        openTenantPopup(item) {
            var vm = this;
            vm.TenantData = item;
            vm.IsTenantPopup = true;
        },
        openModal() {
            this.IsAccountintegration = true;
        },
        async SaveXeroCredentials() {
            var vm = this;
            this.isLoading = true;
            var param = `code=${vm.AccessToken}&state=US&name=Xero&ClientId=${vm.ClientId}&ClientSecret=${vm.ClientSecrat}&CallbackUri=${vm.redirectUri}&WebhooksKey=${encodeURIComponent(vm.Webhookskey)}`
            await DataService.SaveToken(param).then((response) => {
                if (response.data.status == 'Success') {
                    this.ShowAlert(this.$t('AddedSuccess', [this.$t('Integration')]), "Success", true, this.$t('Alert'));
                    vm.FetchData();
                    if (response.data.code && response.data.filePath && response.data.filePath.length > 1) {
                        const itm = {
                            filePath: response.data.filePath,
                            code: response.data.code
                        };
                        vm.openTenantPopup(itm);
                    }
                } else if (parsedData.code == 2) {
                    this.ShowAlert(`${this.driveName}` + " Account Already Integrated", "warning", this.$t('Alert'));
                }
            })
            this.isLoading = false;
        },
        onDriveSelected(driveName) {
            this.selectedDriveName = driveName;
        },
        async FetchData() {
            var vm = this;
            this.isLoading = true;
            const drive = this.selectedDriveName;
            try {
                const params = `TypeCode=${drive}`;
                await DataService.GetIntegrationList(params).then((response) => {
                    vm.Headers.forEach((column) => {
                        column.settings = null;
                        if (["created_at", "status_id", "TenantName"].includes(column.COLUMN_NAME)) {
                            column.settings = {
                                isInSlot: true
                            };
                        }
                    });
                    const resultString = response.data;
                    resultString.forEach(item => {
                        item.TenantName = item.TenantId ? vm.getTenantName(JSON.parse(item.TenantJson), item.TenantId) : null;
                        if (item.created_at) {
                            item.created_at = new Date(item.created_at);
                        }
                    });
                    if (resultString[0] && (resultString[0].TenantId == null || resultString[0].TenantId == '') && resultString[0].status_id == 1001 && resultString[0].name == "Xero") {
                        if (resultString[0].TenantJson && resultString[0].TenantJson.length > 1) {
                            const itm = {
                                filePath: resultString[0].TenantJson,
                                code: resultString[0].id,
                            };
                            vm.openTenantPopup(itm);
                        }
                    }
                    if (response.data.length > 0) {
                        this.OuthList = resultString;
                        this.TotalPages = Math.ceil(this.TotalRecords / this.pageSize);
                        this.CurrentPage = this.pageNum;
                        this.noRecord = false;
                    } else {
                        this.noRecord = true;
                        this.TotalRecords = 0
                        this.OuthList = [];
                    }
                });
            } catch (error) {
                console.error("Error fetching data:", error);
            }
            this.isLoading = false;
        },
        actionButtonClick(callbackfunction, item, index, event) {
            switch (callbackfunction) {
                case 'Refreshtoken':
                    this.callRefreshtoken(item, index);
                    break;
                case 'delete':
                    this.delete(item, index);
                    break;
            }
        },
        renderActions(row) {
            var self = this;
            let rtnActionArray = [];
            self.listActions.forEach(actionItem => {
                let action = {
                    ...actionItem
                };
                switch (action.title) {
                    case 'Refresh':
                        action.isVisible = true;
                        break;
                    case 'Delete':
                        action.isVisible = true;
                        break;
                }
                rtnActionArray.push(action);
            });
            return rtnActionArray;
        },
        async callRefreshtoken(item) {
            var vm = this;
            this.isLoading = true;
            var param = `id=${item.id}`
            await DataService.RefreshToken(param).then((response) => {
                if (response.data.status == 'Success') {
                    vm.ShowAlert(vm.$t('TokenRefreshSuccess'), "Success", true, vm.$t('Alert'));
                    vm.FetchData();
                    if (response.data.code && response.data.filePath && response.data.filePath.length > 1) {
                        const itm = {
                            filePath: response.data.filePath,
                            code: response.data.code
                        };
                        vm.openTenantPopup(itm);
                    }
                } else if (response.data.status == 'Error') {
                    vm.ShowAlert(response.data.message, "failure", true, vm.$t('Alert'));
                } else {
                    vm.ShowAlert(vm.$t("UnknownErrorOccur"), "failure", true, vm.$t('Alert'));
                }
            })
            this.isLoading = false;
        },
        async delete(item) {
            var vm = this;
            this.isLoading = true;
            var param = `companyId=${item.company_id}&userId=${item.created_by}&id=${item.id}`
            await DataService.DeleteIntegration(param).then((response) => {
                const resultString = response.data.Result;
                const parsedArray = JSON.parse(resultString);
                const jsonString = parsedArray[0];
                const parsedData = JSON.parse(jsonString);
                if (parsedData.code == 1) {
                  vm.ShowAlert(vm.$t('DeletedSuccessfully'), "Success", true, vm.$t('Alert'));
                    vm.FetchData();
                }
            })
            this.isLoading = false;
        }
    }
})
</script>